import Vue from "vue";

Vue.filter('convertBalance', function (value: number, to: string, balanceRates: any) {

    return Number((value / balanceRates[to]).toFixed(2))
})

Vue.filter('convertBalanceInverse', function (value: number, from: string, balanceRates: any) {

    return Number((value * balanceRates[from]).toFixed(2))
})
