<template>
  <div id="app">
    <q-scroll-area
      dark
      id="app-scroll"
      class="fullscreen"
      :bar-style="$q.platform.is.mobile ? { width: '0px' } : undefined"
      :thumb-style="$q.platform.is.mobile ? { width: '0px' } : undefined"
      @scroll="onAppScroll"
    >
      <router-view />
    </q-scroll-area>
  </div>
</template>

<script>
import { Component, ProvideReactive, Vue } from "vue-property-decorator";
import { APP_SCROLL_POS_SYMBOL } from "@/constants";

@Component({
  components: {}
})
export default class App extends Vue {
  @ProvideReactive(APP_SCROLL_POS_SYMBOL) appScrollPos = 0;

  onAppScroll({ verticalPosition }) {
    this.appScrollPos = verticalPosition;
  }

  created(){
    this.$i18n.locale = this.$q.lang.getLocale().replace(/^([^-]+)(-.+)?$/, '$1')
		this.$moment.locale(this.$i18n.locale === 'en' ? 'uk' : this.$i18n.locale);
  }
}
</script>

<style lang="scss">
@import "~animate.css";
@import "./styles/reset";
@import "./styles/app.scss";
@import "./assets/css/fontello.css";
</style>
