const ERRORS_OBJECT = {
  "GraphQL error: bot was not found": "0xbwnf00",
  "GraphQL error: Internal error": "0xie0500",
  "GraphQL error: Couldn't connect to server": "0xccs500"
};

export default {
  methods: {
    getErrorCodeByMessage(message: string) {
      return ERRORS_OBJECT[message] || message;
    }
  }
};
